<template>
  <div>
    <el-card class="box-card">
      <h3>用户信息填写</h3>
      <div class="formContainer">
        <el-form
          label-position="right"
          id="addUser"
          :model="addUserForm"
          :rules="rules"
          ref="addUserForm"
          label-width="80px"
        >
          <el-form-item label="用户姓名" style="font-weight:bold" prop="userName">
            <el-input max-length="6" style="width:30%;" v-model="addUserForm.userName"></el-input>
          </el-form-item>
          <el-form-item label="用户昵称" style="font-weight:bold" prop="userNickName">
            <el-input maxlength="7" v-model="addUserForm.userNickName" style="width:30%"></el-input>
          </el-form-item>
          <!-- <el-form-item label="用户地址" style="font-weight:bold" prop="userAddress">
            <v-distpicker v-model="this.addUserForm"
              :province="addUserForm.userAddress.province"
              :city="addUserForm.userAddress.city"
              :area="addUserForm.userAddress.area"
            ></v-distpicker>
          </el-form-item> -->
          <el-form-item label="用户性别" style="font-weight:bold" prop="userSex">
            <el-select v-model="addUserForm.userSex" placeholder="用户性别" @change="change()">
              <el-option label="男" value="0" aria-selected="true"></el-option>
              <el-option label="女" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="用户年龄" style="font-weight:bold" prop="userAge">
            <el-input v-model="addUserForm.userAge" style="width:30%"></el-input>
          </el-form-item>
          <el-form-item label="用户手机" style="font-weight:bold" prop="userPhone">
            <el-input maxlength="11" style="width:30%" v-model="addUserForm.userPhone"></el-input>
          </el-form-item>
          <el-form-item label="用户头像" style="font-weight:bold" prop="userPhoto">
            <el-upload
              class="avatar-uploader"
              action
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="addUserForm.userPhoto" :src="addUserForm.userPhoto" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <!-- <el-input v-model="addUserForm.user_photo"></el-input> -->
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <div class="confirm_cancel_btn">
      <el-button size="medium" type="primary" plain @click="insertUser">确定</el-button>
      <el-button size="medium" plain @click="resetForm('addUserForm')">重置</el-button>
    </div>
  </div>
</template>

<script>
import { validatePhone, isOneToNinetyNine } from "@/utils/validate";
import * as api from "@/api/index";
export default {
  data() {
    return {
      userInfo: {},
      userId: "",
      addUserForm: {
        userName: "",
        userNickName: "",
        userAddress: {
          province: "福建省",
          city: "漳州市",
          area: "龙海市"
        },
        userSex: "",
        userAge: "",
        userPhone: "",
        userPhoto: ""
      },
      rules: {
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 2, max: 5, message: "长度在2到5个字符之间", trigger: "blur" }
        ],
        userNickName: [
          { required: true, message: "请输入用户昵称", trigger: "blur" },
          { min: 2, max: 5, message: "长度在2到5个字符之间", trigger: "blur" }
        ],
        userAddress: [
          { required: false, message: "请输入用户地址", trigger: "blur" }
        ],
        userAge: [
          { required: true, message: "请输入用户年龄", trigger: "blur" },
          { validator: isOneToNinetyNine, trigger: "blur" }
        ],
        userPhone: [
          { required: true, message: "请输入您的电话号码", trigger: "blur" },
          { validator: validatePhone, trigger: "blur" }
        ],
        userSex: [{ required: true, message: "请选择性别", trigger: "blur" }]
      }
    };
  },
  methods: {
    onSelected(){
      this;
    },
    //重新渲染下拉框的值
    change(){
      console.log(this.addUserForm.userSex);
      this.$forceUpdate();
    },
    updateUserInfo() {
      // debugger;
      // let area = this.$route.query.userAddress;
      this.userInfo = this.$route.query;
      if (this.userInfo != null) {
        this.addUserForm.userName = this.userInfo.userName;
        this.addUserForm.userNickName = this.userInfo.userNickName;
        this.addUserForm.userAddress = this.userInfo.userAddress;
        this.addUserForm.userSex = this.userInfo.userSex;
        this.addUserForm.userAge = this.userInfo.userAge;
        this.addUserForm.userPhone = this.userInfo.userPhone;
      }
    },
    //重置表单信息
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    //用户添加方法
    insertUser() {
      this.$refs["addUserForm"].validate(valid => {
        if (!valid) {
          return;
        }
        //转换地址类型，json复杂类型数据后台不容易解析
        let userAddress =
          this.addUserForm.userAddress.province +
          this.addUserForm.userAddress.city +
          this.addUserForm.userAddress.area;
        // console.log(userAddress);
        let params = {
          userName: this.addUserForm.userName,
          userNickName: this.addUserForm.userNickName,
          userAddress: userAddress,
          userSex: this.addUserForm.userSex,
          userAge: this.addUserForm.userAge,
          userPhoto: this.addUserForm.userPhoto,
          userPhone: this.addUserForm.userPhone
        };
        api.insertUser(params).then(res => {
          if (res.data.code === "1") {
            this.$message({
              message: "添加成功",
              type: "success"
            });
          } else if (res.data.code === "0") {
            this.$message({
              message: "添加失败，请重试！",
              type: "error"
            });
          } else {
            this.$message({
              message: "添加失败，请重试！",
              type: "error"
            });
          }
        });
        this.$forceUpdate();
        setTimeout(() => {
          // console.log(this.$router);
          this.$router.push({ path: "/" });
        }, 500);
      });
    }
  },
  mounted() {
    this.updateUserInfo();
  },
  created() {
    if(this.$route.query != null) {
      this.userInfo = this.$route.query;
    }else{
      this.userInfo = {};
    }
    // console.log(this.$route.query);
  }
};
</script>

<style scoped lang="scss">
.box-card {
  width: 100%;
}
.formContainer {
  margin-left: 65px;
}
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader {
  border-color: #409eff;
  width: 145px;
  height: 145px;
}
.el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 145px;
  height: 145px;
  line-height: 145px;
  text-align: center;
}
.avatar {
  width: 145px;
  height: 145px;
  display: block;
}
.confirm_cancel_btn {
  margin: 20px 15px;
}
</style>


